@media screen and (max-width: 1279px) {
    .hScreen{
        display: none;
    }
    .title{
        position: absolute;
        width: 100%;
        height: 20%;
        left: 0%;
        top: 40%;
        text-align: center;
        font-family: 'LaoHouse';
    }
}

@media screen and (min-width: 1280px) {
    .hScreen{
        display: inline;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0px;
        top: 0px;
        background: url(../../images/home.jpg);
        background-size: 100% 100%;
    }
    .title{
        display: none;
    }
    .homeNav{
        display: inline;
        position: absolute;
        width: 100%;
        height: 8%;
        left: 0px;
        top: 0px;
        background-color: #F4F6F7;
        opacity: 0.9;
    }
    .homeLogo{
        display: inline;
        position: absolute;
        height: 80%;
        aspect-ratio: 1 / 1;
        left: 1.4%;
        top: 10%;
        background: url(../../images/logo.jpg);
        background-size: 100% 100%;
        border-radius: 10%;
    }
    .homeLogin{
        display: inline;
        position: absolute;
        width: 12%;
        height: 80%;
        left: 5%;
        top: -12%;
        font-size: 1.3vw;
        text-align: left;
        text-decoration: underline;
        color: #34495E;
    }
    .homeLogin:hover{
        cursor: pointer;
    }
    .contact{
        display: inline;
        position: absolute;
        width: 18%;
        height: 40px;
        right: 9%;
        top: -12%;
        font-size: 1.3vw;
        text-align: right;
        color: #34495E;
    }
    .facebook{
        display: inline;
        position: absolute;
        height: 80%;
        aspect-ratio: 1 / 1;
        right: 5%;
        top: 10%;
        background: url(../../images/fb.png);
        background-size: 100% 100%;
        border-radius: 10%;
    }
    .facebook:hover{
        cursor: pointer;
    }
    .whatsapp{
        display: inline;
        position: absolute;
        height: 80%;
        aspect-ratio: 1 / 1;
        right: 1.4%;
        top: 10%;
        background: url(../../images/wa.png);
        background-size: 100% 100%;
        border-radius: 10%;
    }
    .whatsapp:hover{
        cursor: pointer;
    }
    /* src/components/Slideshow.css */
    .slideshow-container {
        position: absolute;
        width: 52%;
        height: 62%;
        left: calc(50% - 49%);
        top: calc(50% - 35%);
        margin: auto;
        overflow: hidden; /* Hide overflowing slides */
        border-radius: 1%;
        box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    }

    .slides-wrapper {
        display: flex; /* Arrange slides in a row */
        transition: transform 0.5s ease-in-out; /* Smooth slide transition */
    }

    .slide {
        min-width: 100%; /* Each slide takes full width of the container */
        box-sizing: border-box;
    }

    .slide img {
        width: 100%;
        height: auto;
        display: block; /* Remove extra space below image */
        border-radius: 8px;
    }
    .btnRegister{
        display: inline;
        position: absolute;
        overflow: clip;
        width: 18%;
        height: 6%;
        left: calc(50% - 32%);
        top: calc(50% + 28%);
        font-size: 1.4vw;
        font-weight: 1000;
        font-family: 'LaoHouse';
    }
    .trialStudy{
        display: inline;
        position: relative;
        width: 100%;
        height: 100%;
        left: 0px;
        top: 0px;
        background-color: #000000;
        border: none;
    }
    .trialTitle{
        display: inline;
        position: absolute;
        width: 100%;
        height: 10%;                                                                                                                                                                                                                                                           
        left: 0px;
        top: -7%;
        background-color: #000000;
        border: none;
        padding-left: 2%;
        padding-top: 1.8%;
        font-size: 2vw;
        font-weight: 1000;
        color: #FFFFFF;
    }
}