@media screen and (max-width: 1279px) {
    .lScreen{
        display: none;
    }
    .title{
        position: absolute;
        width: 100%;
        height: 20%;
        left: 0%;
        top: 40%;
        text-align: center;
    }
}
@media screen and (min-width: 1280px) {
    .lScreen{
        display: inline;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0px;
        top: 0px;
        background: url(../../images/home.jpg);
        background-size: 100% 100%;
    }
    .title{
        display: none;
    }
    .frame{
        display: inline;
        position: absolute;
        width: 550px;
        height: 350px;
        left: calc(50% - 275px);
        top: calc(50% - 175px);
        opacity: 0.7;
        background-color: #909497;
        border-radius: 6px;
    }
    .logo{
        position: absolute;
        width: 140px;
        height: 140px;
        left: calc(50% - 70px);
        top: calc(50% - 150px);
        background: url(../../images/logo.jpg);
        background-size: 100% 100%;
        border-radius: 6px;
    }
    .txtUsr{
        position: absolute;
        width: 280px;
        height: 34px;
        left: calc(50% - 140px);
        top: calc(50% + 10px);
        background-color: #FFFFFF;
        border-radius: 6px;
    }
    .txtPwd{
        position: absolute;
        width: 280px;
        height: 34px;
        left: calc(50% - 140px);
        top: calc(50% + 54px);
        background-color: #FFFFFF;
        border-radius: 6px;
    }
    .btnLogin{
        position: absolute;
        width: 180px;
        height: 34px;
        left: calc(50% - 90px);
        top: calc(50% + 98px);
        border-radius: 6px;
    }
}