body{
    display: absolute;
    position: absolute;
    width: 100%;
    height: 100%;
    background-color: #FFFFFF;
}
.logoAd{
        display: absolute;
        position: absolute;
        width: 340px;
        height: 340px;
        left: calc(50% - 50px);
        top: calc(50% - 150px);
        background: url(../../images/logo.jpg);
        background-size: 100% 100%;
        border-radius: 6px;
}