@media screen and (max-width: 1279px) {
    .rScreen{
        display: none;
    }
    .title{
        position: absolute;
        width: 100%;
        height: 20%;
        left: 0%;
        top: 40%;
        text-align: center;
        font-family: 'LaoHouse';
    }
}

@media screen and (min-width: 1280px) {
    .rScreen{
        display: inline;
        position: absolute;
        width: 100%;
        height: 100%;
        left: 0px;
        top: 0px;
        background: url(../../images/home.jpg);
        background-size: 100% 100%;
    }
    .title{
        display: none;
    }
    .frameRegis{
        display: inline;
        position: absolute;
        width: 28%;
        height: 76%;
        top: calc(50% - 38%);
        left: calc(50% - 14%);
        opacity: 0.7;
        background-color: #909497;
        border-radius: 10px;
    }
    .titleRegis{
        display: inline;
        position: absolute;
        width: 38%;
        height: 4%;
        top: calc(50% - 42%);
        left: calc(50% - 19%);
        color: #FFFFFF;
        font-size: 3.2vw;
        font-weight: bold;
        text-align: center;
    }
    .txtStdName{
        position: absolute;
        width: 23.6%;
        height: 4.8%;
        top: calc(50% - 23%);
        left: calc(50% - 11.8%);
    }
    .txtStdPhone{
        position: absolute;
        width: 23.6%;
        height: 4.8%;
        top: calc(50% - 17%);
        left: calc(50% - 11.8%);
    }
    .txtStdPwd{
        position: absolute;
        width: 23.6%;
        height: 4.8%;
        top: calc(50% - 11%);
        left: calc(50% - 11.8%);
    }
    .txtStdRePwd{
        position: absolute;
        width: 23.6%;
        height: 4.8%;
        top: calc(50% - 5%);
        left: calc(50% - 11.8%);
    }
    .lblPrice{
        position: absolute;
        width: 23.6%;
        height: 2%;
        top: calc(50% - 2%);
        left: calc(50% - 11.6%);
        color: #FFFFFF;
        font-size: 1.5vw;
        font-weight: 1000;
        text-align: left;
    }
    .lblPayMethod{
        position: absolute;
        width: 23.6%;
        height: 2%;
        top: calc(50% + 8%);
        left: calc(50% - 11.6%);
        color: #FFFFFF;
        font-size: 1.4vw;
        font-weight: 1000;
        text-align: left;
        text-decoration: underline;
    }
    .btnRegis{
        display: inline;
        position: absolute;
        width: 14%;
        height: 5.4%;
        top: calc(50% + 28%);
        left: calc(50% - 7%);
        font-size: large;
        font-weight: 1.4vw;
        font-family: 'LaoHouse';
    }
}
@media screen and (max-width: 1279px) {
    .frameRegis{
        display: inline;
        position: absolute;
        width: 96%;
        height: 96%;
        top: 2%;
        left: 2%;
        opacity: 0.9;
        background-color: #909497;
        border-radius: 10px;
    }
    .titleRegis{
        display: inline;
        position: absolute;
        width: 800px;
        height: 100px;
        top: calc(50% - 800px);
        left: calc(50% - 400px);
        color: #FFFFFF;
        font-size: 7rem;
        font-weight: bold;
        text-align: center;
    }
}