@font-face {
  font-family: 'LaoHouse'; /* Choose a name for your font */
  src: url('./fonts/Lao_House.ttf') format('truetype');
  font-weight: normal; /* Define the weight for this specific font file */
  font-style: normal;
}

::placeholder{
  font-family: 'LaoHouse';
  font-weight: 900;
}

p{
  font-family: 'LaoHouse';
  font-weight: 900;
}

h1{
  font-family: 'LaoHouse';
  font-weight: 900;
}

.ant-input{
  font-family: 'LaoHouse';
  font-weight: 900;
}

.ant-spin-text {
  font-family: 'LaoHouse';
  font-size: 14px;
  font-weight: 900;
}

.ant-btn{
  font-family: 'LaoHouse';
  font-size: large;
  font-weight: 900;
}

.ant-menu-item{
  font-family: 'LaoHouse';
  font-size: 16px;
  font-weight: 900;
}

.ant-select-selector,
.ant-select-selection-item{
  font-family: 'LaoHouse';
  font-size: 16px;
  font-weight: 900;
}

.ant-select-item-option-content{
  font-family: 'LaoHouse';
  font-size: 16px;
  font-weight: 900;
}

.ant-divider-inner-text{
  font-family: 'LaoHouse';
  font-weight: 900;
}

body {
  margin: 0;
}

code {
  font-family: 'LaoHouse';
}